import React, { SyntheticEvent, useCallback, useEffect, useState } from 'react';
import classNames from 'classnames';

// Components
import Button, { ButtonProps } from '../Button';
import Container, { ContainerProps } from '../Container';
import Popover, { PopoverProps } from '../Popover';

// Styles
import './PopoverButton.scss';

// Types
import { TestMetadata } from 'types';

export interface PopoverButtonProps {
  /** If need to open/close outside of the button */
  isOpen?: boolean;
  /** Gets called when popover button opens/closes */
  handleToggle?: (open: boolean, e?: SyntheticEvent) => void;
  /** Sets disabled and fetching on button */
  isDisabled?: boolean;
  /** Text override, default is empty */
  buttonText?: string;
  /** Icon override, default is ellipsis */
  buttonIcon?: string;
  /** Button props override if more changes are needed */
  buttonProps?: ButtonProps;
  /** Popover props override if more changes are needed */
  popoverProps?: Omit<PopoverProps, 'children' | 'popoverContent'>;
  /** Container props override if more changes are needed */
  containerProps?: Omit<ContainerProps, 'children'>;
  /* Menu Portal */
  portal?: HTMLElement | null;
  /** Classname for the Popover component */
  className?: string;
  /** Classname for the Popover wrapper */
  contentClassName?: string;
  /** Testmetadata for the sections inside the component */
  testMetadata?: {
    container?: TestMetadata;
    popover?: {
      popover?: TestMetadata;
      popoverContent?: TestMetadata;
    };
    button?: TestMetadata;
  };
}

const PopoverButton: React.FC<PopoverButtonProps> = ({
  isOpen,
  children,
  isDisabled,
  buttonText,
  buttonIcon,
  containerProps,
  buttonProps,
  popoverProps,
  handleToggle,
  portal,
  className,
  testMetadata,
  contentClassName,
}) => {
  const [open, setOpen] = useState<boolean>(false);

  useEffect(() => {
    if (isOpen !== undefined) setOpen(isOpen);
  }, [isOpen, setOpen]);

  const handleOpenToggle = useCallback(
    (newState: boolean, e?: SyntheticEvent) => {
      if (typeof handleToggle !== 'undefined') {
        handleToggle(newState, e);
      }

      setOpen(newState);
    },
    [handleToggle],
  );

  return (
    <Container
      className="sb-popover-button__container"
      testMetadata={testMetadata?.container}
      {...containerProps}
    >
      <Popover
        visible={open}
        popoverContent={children}
        className={classNames('sb-popover-button__popover', className)}
        contentClassName={classNames(
          'sb-popover-button__wrapper',
          contentClassName,
        )}
        tippyProps={{
          arrow: true,
          interactive: true,
          theme: 'light-border',
          placement: 'bottom-end',
          animation: process.env.NODE_ENV === 'test' ? false : 'fade',
          onClickOutside: () => handleOpenToggle(false),
          ...((portal || typeof window !== 'undefined') && {
            appendTo: portal ? portal : 'parent',
          }),
        }}
        testMetadata={testMetadata?.popover}
        {...popoverProps}
      >
        <Button
          className="sb-popover-button__button"
          styleType="standard"
          iconPlacement={buttonText ? 'after' : undefined}
          iconOnly={!buttonText}
          iconType={
            buttonIcon ??
            (buttonText ? 'fa-solid fa-chevron-down' : 'fa-solid fa-ellipsis')
          }
          sizeType="smaller"
          onClick={(e) => handleOpenToggle(!open, e)}
          type="button"
          disabled={isDisabled}
          testMetadata={testMetadata?.button}
          {...buttonProps}
        >
          {buttonText}
        </Button>
      </Popover>
    </Container>
  );
};

export default PopoverButton;
